.records-window {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    z-index: 5;
    background-color: #00000000;
    
    &::before {
        content: '';
        background-color: #000;
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        opacity: 0.80;
    }

    .records-window-content {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        display: grid;
        grid-template-columns: 70% auto;
        gap: 20px;
        border-radius: 15px;
        width: 100%;
    }

    .left, .right {
        max-height: 90vh;
        overflow-y: auto;

        &::-webkit-scrollbar {
            display: none;
        }
    }

    .left {
        .show-level {
            border-radius: 15px;
            padding: 20px;
            background-color: #fff;
            text-align: center;
            margin-bottom: 20px;

            p {
                margin-bottom: 20px;
            }

            iframe {
                border-radius: 15px;
                box-shadow: 15px 15px 1rem #00000050;
            }

            .close-window {
                text-align: right;
                width: 100%;
                display: inline-block;
                color: #595959;
                cursor: pointer;
                transition: .3s color;

                &:hover {
                    color: #000;
                }
            }
        }

        .level-info {
            border-radius: 15px;
            padding: 20px;
            background-color: #fff;
            text-align: center;
            margin-bottom: 20px;

            .level-info-list {
                display: flex;
                flex-wrap: wrap;
                justify-content: space-around;

                li {
                    margin-top: 30px;
                    margin-left: 30px;
                    margin-right: 30px;
                    padding: 20px;
                    border-radius: 15px;
                    border: 1px dashed #bababa;
                }
            }
        }

        .records {
            border-radius: 15px;
            padding: 20px;
            background-color: #fff;
            text-align: center;

            .records-list {
                text-align: left;

                li a {
                    color: #595959;
                    transition: .3s color;

                    &:hover {
                        color: #000;
                    }
                }
            }
        }
    }

    .right {
        .moderators {
            border-radius: 15px;
            padding: 20px;
            background-color: #fff;
            text-align: center;
            margin-bottom: 20px;

            .dl-moderators-list {
                text-align: left;

                li {
                    display: flex;

                    img {
                        width: 25px;
                        margin-right: 10px;
                    }
                }
            }
        }

        .sending-record {
            border-radius: 15px;
            padding: 20px;
            background-color: #fff;
            text-align: center;

            p {
                text-align: left;
            }
        }
    }
}