* {
    padding: 0;
    margin: 0;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    box-sizing: border-box;
    color: #000;
    user-select: none;
}

html, body {
    overflow-x: hidden;
}

body {
    min-height: 100vh;
}

li {
    list-style: none;
}

a {
    text-decoration: none;
}

input, button, textarea {
    outline: none;
    background: none;
    border: none;
}

*::-webkit-scrollbar {
    width: 6px;
}
  
*::-webkit-scrollbar-track {
    background: #f0f0f0;
}
  
*::-webkit-scrollbar-thumb {
    background: #606060;
    border-radius: 10px;
}
  
*::-webkit-scrollbar-thumb:hover {
    background: #555;
}

.container {
    margin: 0 auto;
    padding-left: 20px;
    padding-right: 20px;
    max-width: 1300px;
}

h1 {
    font-size: 32px;
}

b {
    color: #668fff;
}