.prev-screen {
    padding-top: 200px;
    background-image: linear-gradient(to left top, #e4efff, #d0e2fc);
    padding-bottom: 270px;
    position: relative;
    
    .wave-trans {
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
    }
}

.prev-screen-body {
    display: flex;
    align-items: center;
    justify-content: space-between;

    .prev-screen-body-img {
        position: relative;
        min-width: 650px;
        max-width: 650px;
        margin-left: 60px;

        .main {
            width: 100%;
            border-radius: 15px;
            position: relative;
            z-index: 1;
            box-shadow: 15px 15px 1rem #0000004c;
        }

        .cube1, .cube2, .cube3 {
            position: absolute;
            z-index: 2;
        }

        .cube1 {
            bottom: -20px;
            left: -20px;
            width: 75px;
            height: 75px;
            transform: rotate(-15deg);
        }

        .cube2 {
            bottom: 30px;
            right: -40px;
            width: 125px;
            height: 125px;
            transform: rotate(5deg);
        }

        .cube3 {
            top: -60px;
            left: 60px;
            width: 100px;
            height: 100px;
            transform: rotate(-5deg);
        }

        &::before {
            content: '';
            position: absolute;
            bottom: -75px;
            left: -75px;
            width: 200px;
            height: 200px;
            border-radius: 50%;
            background-color: #8eacff;
            z-index: 0;
        }

        &::after {
            content: '';
            position: absolute;
            top: -75px;
            right: -75px;
            width: 400px;
            height: 400px;
            border-radius: 50%;
            background-color: #8eacff;
            z-index: 0;
        }
    }

    .prev-screen-body-text {
        position: relative;
        z-index: 1;

        h1 span {
            color: #668fff;
            font-weight: 800;
        }

        h1 {
            margin-bottom: 20px;
        }
        
        p {
            color: #626262;
            margin-bottom: 20px;
        }

        .buttons {
            display: flex;

            button {
                margin-right: 20px;
                margin-bottom: 20px;
                border-radius: 10px;
                padding: 10px 15px;
                cursor: pointer;
                display: flex;
                align-items: center;
                transition: .3s background-color;
    
                svg {
                    width: 16px;
                    margin-right: 5px;
                }
    
                span {
                    color: #fff;
                    font-size: 16px;
                }
            }

            button:nth-child(1) {
                background-color: #668fff;
        
                &:hover {
                    background-color: #4e6dc3;
                }
            }

            button:nth-child(2) {
                background-color: #668fff;
        
                &:hover {
                    background-color: #4e6dc3;
                }
            }
        }
    }
}

.we-have {
    position: relative;
    z-index: 2;
    padding-top: 200px;
    padding-bottom: 200px;
    text-align: center;

    h1 {
        margin-bottom: 80px;

        span {
            color: #668fff;
            font-weight: 800;
        } 
    }

    .we-have-list {
        display: flex;
        align-items: center;
        justify-content: space-between;

        li {
            position: relative;

            svg {
                height: 175px;
                width: 175px;
                margin-bottom: 20px;
                background-image: linear-gradient(to left top, #668fff, #7a66ff);
                padding: 20px;
                border-radius: 50%;
                box-shadow: 15px 15px 1rem #00000036;
            }

            .cube4 {
                position: absolute;
                top: -10px;
                left: -10px;
                width: 85px;
                height: 85px;
                transform: rotate(-10deg);
                z-index: -1;
            }

            .cube5 {
                position: absolute;
                top: 100px;
                right: -10px;
                width: 85px;
                height: 85px;
                transform: rotate(10deg);
                z-index: 2;
            }

            .cube6 {
                position: absolute;
                top: -10px;
                left: -10px;
                width: 65px;
                height: 65px;
                transform: rotate(-15deg) scaleX(-1);
                z-index: 2;
            }

            .cube7 {
                position: absolute;
                top: -10px;
                right: -10px;
                width: 75px;
                height: 75px;
                transform: rotate(10deg) scaleX(-1);
                z-index: 2;
            }

            .cube8 {
                position: absolute;
                top: 100px;
                left: -10px;
                width: 75px;
                height: 75px;
                transform: rotate(-10deg);
                z-index: 2;
            }
        }
    }
}

