.dl-list {
    margin-top: 200px;
    margin-bottom: 200px;

    li {
        display: flex;
        align-items: center;
        background-color: #fff;
        border-radius: 15px;
        padding: 20px;
        box-shadow: 15px 15px 1rem #00000018;
        border: 1px #bababa dashed;
        cursor: pointer;
        margin-bottom: 30px;
        transition: .3s transform;

        &:hover {
            transform: scale(102%);
        }

        img {
            width: 180px;
            height: 110px;
            margin-right: 20px;
            border-radius: 15px;
            background-color: #bababa;
        }
    }
}